export default class OTicker {
    constructor(stopTime = 1) {
        this._timer = PIXI.timerManager.createTimer(stopTime * 1000.0);
        this._isDestroyed = false;
        this._timer.on("end", this._onEndHandler, this);
        this._timer.on("repeat", this._onEndRepeatHandler, this);
    }

    /** @public */
    start() {
        if (this._isDestroyed) return null;
        if (!this.isStarted) this._timer.start();
        return this;
    }

    /** @public */
    stop() {
        if (this._isDestroyed) return null;
        if (this.isStarted) this._timer.stop();
        return this;
    }

    /** @public */
    reset() {
        if (this._isDestroyed) return null;
        this._timer.reset();
    }

    /**     * @public     */
    destroy() {
        if (!this._isDestroyed) {
            if (!this.isEnded) this._timer.stop();
            this._isDestroyed = true;
            this._timer.remove(this);
            this._timer.manager = null;
            this._timer.removeAllListeners("end");
            this._timer.removeAllListeners("start");
            this._timer.removeAllListeners("repeat");
            this._timer.removeAllListeners("stop");
            this._timer = null;

            this._onComplete = null;
            this._context = null;
            this._params = null;
        }

        return this;
    }

    /**     * @public     */
    on(fnc, c, params = []) {
        if (this._isDestroyed) return null;
        this._onComplete = fnc;
        this._context = c;
        this._params = params;
        params.push(this);
    }

    /** @public */
    onStart(fnc, c, once = false) {
        if (once)
            this._timer.once("start", fnc, c);
        else
            this._timer.on("start", fnc, c);

    }

    /** @public */
    onEnd(fnc, c, once = false) {
        if (once)
            this._timer.once("end", fnc, c);
        else
            this._timer.on("end", fnc, c);

    }

    /** @public */
    onRepeat(fnc, c, once = false) {
        if (once)
            this._timer.once("repeat", fnc, c);
        else
            this._timer.on("repeat", fnc, c);

    }

    /** @public */
    onStop(fnc, c, once = false) {
        if (once)
            this._timer.once("stop", fnc, c);
        else
            this._timer.on("stop", fnc, c);

    }

    /** @public */
    offStart(fnc, c) {
        this._timer.off("start", fnc, c);

    }

    /** @public */
    offEnd(fnc, c) {
        this._timer.off("end", fnc, c);

    }

    /** @public */
    offRepeat(fnc, c) {
        this._timer.off("repeat", fnc, c);

    }

    /** @public */
    offStop(fnc, c) {
        this._timer.on("stop", fnc, c);

    }

    /** @private */
    _onEndHandler(elapsed) {
        if (this._onComplete)
            this._onComplete.apply(this._context, this._params);
        if (this._isDestroyed) return null;
        if (this.expire)
            this.destroy();
    }

    /**     * @private     */
    _onEndRepeatHandler(elapsed) {
        if (this._onComplete)
            this._onComplete.apply(this._context, this._params);
    }

    set time(value) {
        this._timer.time = value * 1000.0;
    }

    get time() {
        return this._timer.time * 1000.0;
    }

    get active() {
        return this._timer.active;
    }

    get isStarted() {
        return this._timer.isStarted;
    }

    get isEnded() {
        return this._timer.isEnded;
    }

    set expire(value) {
        this._timer.expire = value;
    }

    get expire() {
        return this._timer.expire;
    }

    set delay(value) {
        this._timer.delay = value * 1000.0;
    }

    get delay() {
        return this._timer.delay * 1000.0;
    }

    set repeat(value) {
        this._timer.repeat = value;
    }

    get repeat() {
        return this._timer.repeat;
    }

    set loop(value) {
        this._timer.loop = value;
    }

    get loop() {
        return this._timer.loop;
    }

    get completeParams() {
        return this._params;
    }
}
