    /**     * @public     */
    button(type, parent = null, x = 0, y = 0,
           out = null, atlas = null, over = null, down = null, block = null,
           externalMethod = null, methodParams = null, debug = 0) {
        let btn;
        type = type || OmyConst.OMY_BUTTON;
        switch (type) {
            case OmyConst.OMY_BUTTON: {
                btn = new OButton(out, atlas, over, down, block);
                break;
            }
            case OmyConst.OMY_BUTTON_TWEEN: {
                btn = new OButtonTween(out, atlas, over, down, block);
                break;
            }
        }

        btn.setXY(x, y);

        if (externalMethod)
            btn.externalMethod(externalMethod, methodParams);

        if (parent)
            parent.addChild(btn);

        if (debug) {
            this.timer(.1, this.regDebugMode, this, 0, false, true, 0, [btn]);
        }

        return btn;
    }

    /**     * @public     */
    buttonJson(parent = null, json = null, externalMethod = null, methodParams = null, btnClass = null) {
        if (json.hasOwnProperty("active") && !json["active"]) return null;
        json = OMY.OMath.jsonCopy(json);

        let btn;
        let type = btnClass || json["type"];
        let atlas = json["atlas"] || null;
        switch (type) {
            case OmyConst.OMY_BUTTON: {
                btn = new OButton(json["out"], atlas, json["over"], json["down"], json["block"], json);
                break;
            }
            case OmyConst.OMY_BUTTON_TWEEN: {
                btn = new OButtonTween(json["out"], atlas, json["over"], json["down"], json["block"], json);
                break;
            }
            case OmyConst.OMY_CHECK_BOX_TWEEN: {
                btn = new OCheckBoxTween(json["out"], atlas, json);
                break;
            }
            case OmyConst.OMY_CHECK_BOX: {
                btn = new OCheckBox(json["out"], atlas, json["over"], json["down"], json["block"], json);
                break;
            }
            default: {
                btn = new btnClass(json["out"], atlas, json["over"], json["down"], json["block"], json);
                break;
            }
        }
        btn.json = json;
        btn.name = json["name"] || "b_" + OMath.uniqueID();
        btn.setXY(json["x"] || 0, json["y"] || 0);
        btn.rotation = OMath.toRadians(json["rotate"]) || 0;
        btn.alpha = json.hasOwnProperty("alpha") ? json["alpha"] : 1;
        btn.visible = json.hasOwnProperty("visible") ? json["visible"] : true;
        if (json["scale"])
            btn.setScale(json["scale"], json["scale"]);
        else
            btn.setScale(json["scaleX"] || 1, json["scaleY"] || 1);
        if (json["anchor"])
            btn.anchor.set(json["anchor"]);
        if (json["anchorX"] || json["anchorY"])
            btn.setAnchor(json["anchorX"] || 0, json["anchorY"] || 0);

        if (json["hintAreaRect"])
            this.hintArea(btn, json["hintAreaRect"], json["hintAreaRect"]["type"], json["hintAreaRect"]["debug"]);
        if (json["label"])
            btn.label = this.textJson(null, json["label"]);
        if (json["hintLabel"])
            btn.hintLabel = this.textJson(null, json["hintLabel"]);
        if (json["oneClick"])
            btn.oneClick = json["oneClick"];
        if (json["soundUp"])
            btn.soundUp = json["soundUp"];
        if (json["soundOver"])
            btn.soundOver = json["soundOver"];
        if (externalMethod)
            btn.externalMethod(externalMethod, methodParams);
        if (parent)
            parent.addChild(btn);
        if (json["tint"])
            btn.tint = json["tint"];
        if (json["debug"]) {
            this.timer(.1, this.regDebugMode, this, 0, false, true, 0, [btn]);
        }
        this.createEntities(btn, json);

        return btn;
    }

    /** @public */
    btnJson(parent, json, buttonClass) {
        if (!parent) {
            Omy.error("Button can not be created without parent object");
            return;
        }
        if (!json) {
            Omy.error("Button can not be created without configuration object");
            return;
        }
        if (!buttonClass) {
            Omy.error("Button can not be created without buttonClass");
            return;
        }
        if (!(buttonClass.prototype instanceof OButtonMod)) {
            Omy.error("Button class must be extended from OButtonMod");
            return;
        }

        json = OMY.OMath.jsonCopy(json);
        return new buttonClass(parent, json);
    }

    /**
     * Create container from json
     * @public
     * @param {*} parent
     * @param {Object} json
     * @returns {OContainer}
     */
    containerJson(parent, json = null) {
        if (json.hasOwnProperty("active") && !json["active"]) return null;
        json = OMY.OMath.jsonCopy(json);

        let cont = this.container(parent, json["x"], json["y"], false,
            ((json.hasOwnProperty("interactiveChildren")) ? json["interactiveChildren"] : true));
        cont.json = json;
        cont.name = json["name"] || "c_" + OMath.uniqueID();
        if (json["scale"]) {
            cont.scale.set(json["scale"]);
        } else {
            if (json["scaleX"]) {
                cont.scaleX = json["scaleX"];
            }
            if (json["scaleY"]) {
                cont.scaleY = json["scaleY"];
            }
        }

        if (json["rotate"]) cont.rotation = OMath.toRadians(json["rotate"]);

        cont.alpha = json.hasOwnProperty("alpha") ? json["alpha"] : 1.0;
        cont.visible = json.hasOwnProperty("visible") ? json["visible"] : true;

        this.createEntities(cont, json);

        if (json.hasOwnProperty("centered") && json["centered"])
            cont.setToCenter();
        if (json.hasOwnProperty("align"))
            cont.setAlign(json["align"]);
        if (json.hasOwnProperty("alignContainer"))
            cont.createAlign(json["alignContainer"]);

        if (json.hasOwnProperty("anchor"))
            cont.setAnchor(json["anchor"], json["anchor"]);
        if (json.hasOwnProperty("anchorX") || json.hasOwnProperty("anchorY"))
            cont.setAnchor(json["anchorX"] || 0, json["anchorY"] || 0);
        if (json["soundUp"])
            cont.soundUp = json["soundUp"];
        if (json["soundOver"])
            cont.soundOver = json["soundOver"];
        if (json["tint"])
            cont.tint = json["tint"];

        if (json["debug"]) {
            this.timer(.1, this.regDebugMode, this, 0, false, true, 0, [cont]);
        }

        if (json.hasOwnProperty("interactive"))
            cont.interactive = json["interactive"];
        if (json.hasOwnProperty("cacheAsBitmap"))
            this.timer(.3, () => {
                cont.cacheAsBitmap = json["cacheAsBitmap"];
            }, cont);

        return cont;
    }

