    /**
     * Add mask graphic by configuration object
     * @param {*} parent
     * @param {Object} json - Configuration object
     * @param {number} json.x=0
     * @param {number} json.y=0
     * @param {number} json.width=100
     * @param {number} json.height=100
     * @param {boolean} json.debug=false
     * @returns {OGraphic}
     */
    maskRectJson(parent, json) {
        return this.maskRect(parent, json["x"], json["y"], json["width"], json["height"], json["debug"]);
    }

    /**
     * Add mask graphic
     * @param {*} parent
     * @param {number} x=0
     * @param {number} y=0
     * @param {number} width=100
     * @param {number} height=100
     * @param {boolean} debug=false
     * @returns {OGraphic}
     */
    maskRect(parent = null, x = 0, y = 0, width = 100, height = 100, debug = false) {
        let mask = this.graphicRectTexture(width, height);
        mask.x = x;
        mask.y = y;
        parent && parent.addChild(mask);

        mask.interactChild = false;
        mask.interactive = false;

        if (debug) {
            this.timer(.1, this.regDebugMode, this, 0, false, true, 0, [mask]);
            if (Number(debug) !== 2) {
                return null;
            }
        }
        return mask;
    }

    /**
     * Add hit area to graphic object
     * @param {*} parent
     * @param {Object} json
     * @param {number} json.x=0
     * @param {number} json.y=0
     * @param {number} json.width=0
     * @param {number} json.height=0
     * @param {number} json.type=0
     * @param {boolean} json.debug=false
     * @returns {PIXI.Rectangle | PIXI.Circle | PIXI.Ellipse | PIXI.Polygon | PIXI.RoundedRectangle }
     */
    hitAreaJson(parent, json) {
        return this.hintArea(parent, json, json["type"], json["debug"]);
    }

    /**
     * @param parent
     * @param json = {x:0, y:0, width:0, height:0}
     * @param type Rectangle:0 | Circle:1 | Ellipse:2 | Polygon:3 | RoundedRectangle:4
     * @param {boolean}debug =false
     * @returns {PIXI.Rectangle | PIXI.Circle | PIXI.Ellipse | PIXI.Polygon | PIXI.RoundedRectangle }
     */
    hintArea(parent, json = null, type = 0, debug = false) {
        let res = null;
        if (debug) {
            let graphic = new OGraphic();
            graphic.beginFill(0xffffff, 0.3);
            switch (type) {
                case 0: {
                    graphic.drawRect(0, 0, json.width, json.height);
                    break;
                }
                case 1: {
                    graphic.drawCircle(0, 0, json.radius);
                    break;
                }
                case 2: {
                    graphic.drawEllipse(0, 0, json.width, json.height);
                    break;
                }
                case 3: {
                    graphic.drawPolygon(json.points);
                    break;
                }
                case 4: {
                    graphic.drawRoundedRect(0, 0, json.width, json.height, json.radius);
                    break;
                }
            }
            graphic.endFill();
            graphic.setXY(json.x, json.y);
            parent.addChild(graphic);
            this.timer(.1, this.regDebugMode, this, 0, false, true, 0, [graphic]);
        }
        switch (type) {
            case 0: {
                res = new PIXI.Rectangle(json.x, json.y, json.width, json.height);
                break;
            }
            case 1: {
                res = new PIXI.Circle(json.x, json.y, json.radius);
                break;
            }
            case 2: {
                res = new PIXI.Ellipse(json.x, json.y, json.width, json.height);
                break;
            }
            case 3: {
                res = new PIXI.Polygon(json.points);
                break;
            }
            case 4: {
                res = new PIXI.RoundedRectangle(json.x, json.y, json.width, json.height, json.radius);
                break;
            }
        }
        parent.hitArea = res;
        return res;
    }

